/*Card*/
.card {
	border-color: $grey_light_4;
	box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
	margin-bottom: 0.875rem;
	border-radius: 0.5rem;
	.card-header {
		background: transparent;
		border-color: $grey_light_4;
		padding: 0.75rem 1.25rem;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
		justify-content: space-between;
		-webkit-justify-content: space-between;
		-ms-flex-pack: space-between;
		> h1,
		> h2,
		> h3,
		> h4,
		> h5,
		> h6,
		> .h1,
		> .h2,
		> .h3,
		> .h4,
		> .h5,
		> .h6 {
			margin-bottom: 0;
		}
		&.card-header-bold {
			font-size: 0.875rem;
			text-transform: uppercase;
			font-weight: 600;
		}
		&.card-header-wth-bg {
			background: $grey_light_5;
			border-color: transparent;
			&.card-header-action {
				.card-action-wrap {
					color: $white;
				}
			}
		}
		&.card-header-action {
			.card-action-wrap {
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				-webkit-box-align: center;
				-ms-flex-align: center;
				align-items: center;
				> a.collapsed {
					@include transform(rotate(180deg));
				}
			}
			&.card-header-wth-text {
				align-items: flex-start;
				-webkit-box-align: flex-start;
				-ms-flex-align: flex-start;
				border-radius: calc(0.5rem - 1px) calc(0.5rem - 1px) 0 0;
			}
		}
		&.card-header-wth-bg-inv {
			color: $white;
			background: $primary;
			border-color: transparent;
			h1,
			h2,
			h3,
			h4,
			h5,
			h6,
			.h1,
			.h2,
			.h3,
			.h4,
			.h5,
			.h6 {
				color: $white;
			}
		}

		&:first-child {
			border-radius: calc(0.5rem - 1px) calc(0.5rem - 1px) 0 0;
		}
		.nav-tabs.card-header-tabs {
			border-bottom: 0;
		}
	}
	&.sticky-card-header {
		.card-header {
			top: 64px;
			position: -webkit-sticky;
			position: sticky;
			z-index: 100;
		}
	}
	.card-text {
		margin-bottom: 1rem;
		&:last-child {
			margin-bottom: 0;
		}
	}
	.card-title {
		font-size: 1rem;
		color: $black_2;
		font-weight: 500;
	}
	.card-subtitle {
		font-size: 0.875rem;
	}
	.card-body {
		padding: 1.25rem;
		min-height: 1px;
		> .card-action-wrap {
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
			-ms-flex-align: center;
			align-items: center;
			position: absolute;
			right: 14px;
			top: 8px;
			.btn {
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				flex-shrink: 0;
			}
		}
		> .card-close {
			position: absolute;
			right: 14px;
			top: 10px;
			cursor: pointer;
			a {
				color: $black_2;
				@include transition(0.2s ease);
				margin-left: 1rem;
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				opacity: 0.6;
				font-size: 1.125rem;
				&:hover {
					opacity: 1;
				}
			}
		}
	}
	&.close-over {
		.btn-close {
			position: absolute;
			top: -10px;
			right: -10px;
		}
	}
	&.card-selected {
		position: relative;
		border-color: $primary;
		&:after {
			height: 22px;
			width: 22px;
			background: $primary;
			color: $white;
			border-radius: 50%;
			display: -webkit-box;
			display: -ms-flexbox;
			display: flex;
			-webkit-box-align: center;
			-ms-flex-align: center;
			align-items: center;
			justify-content: center;
			-webkit-justify-content: center;
			-ms-flex-pack: center;
			cursor: pointer;
			font: normal normal normal 16px/1 "Material-Design-Iconic-Font";
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
			content: "\f26b";
			top: -10px;
			right: -10px;
			line-height: 0;
			position: absolute;
		}
	}
	.card-footer {
		background: transparent;
		border-color: $grey_light_4;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
		padding: 0.75rem 1.25rem;
		&:last-child {
			border-radius: 0 0 calc(0.5rem - 1px) calc(0.5rem - 1px);
		}
	}
	.card-img-bg {
		overflow: hidden;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
	}
	&.card-refresh {
		position: relative;
		.refresh-container {
			@include background-opacity($white, 0.9);
			height: 100%;
			left: 0;
			position: absolute;
			top: 0;
			width: 100%;
			display: none;
			z-index: 5;
			cursor: wait;
			border-radius: 0.5rem;
			overflow: hidden;
			.loader-pendulums {
				font-size: 2rem;
			}
		}
	}
	&.fullscreen {
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		margin: 0;
		position: fixed;
		overflow-y: auto;
		z-index: 1040;
		border-radius: 0;
		@include transition(all 0.2s ease);
	}
	&.card-lg {
		.card-header,
		.card-footer {
			padding: 1rem 1.5rem;
		}
		.card-body {
			padding: 1.5rem;
		}
	}
	&.card-sm {
		.card-header,
		.card-footer {
			padding: 0.625rem 1rem;
		}
		.card-body {
			padding: 1rem;
		}
	}
	&.card-shadow {
		border: none;
		box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1);
	}
	&.card-flush {
		border: none;
		box-shadow: none;
	}
	&.card-border {
		border: 1px solid $grey_light_4;
		box-shadow: none;
	}
	&.card-simple {
		.card-header,
		.card-footer {
			border: none;
		}
	}
	&.card-wth-line {
		padding-top: 0.25rem;
		border-top: 0;
		.card-line {
			position: absolute;
			top: 0;
			height: 30px;
			width: 100%;
			border-top-left-radius: 0.5rem;
			border-top-right-radius: 0.5rem;
		}
		.card-body {
			position: relative;
			z-index: 1;
			background: $white;
			border-top-left-radius: 0.5rem;
			border-top-right-radius: 0.5rem;
		}
	}
	&.card-wth-progress {
		.progress {
			background: transparent;
			border-top-left-radius: 0.5rem;
			border-top-right-radius: 0.5rem;
		}
	}
	&.card-wth-tabs {
		> .card-header {
			padding-bottom: 0.5rem;
		}
	}
}
.card-img,
.card-img-top {
	border-top-left-radius: calc(0.5rem - 1px);
	border-top-right-radius: calc(0.5rem - 1px);
}
.card-img,
.card-img-bottom {
	border-bottom-right-radius: calc(0.5rem - 1px);
	border-bottom-left-radius: calc(0.5rem - 1px);
}
.card-group > .card {
	margin-bottom: 0.875rem;
}
.hk-wrapper.hk-menu-toggle {
	.card.fullscreen {
		left: 0;
	}
}
@media (max-width: 1199px) {
	.card.fullscreen {
		left: 0;
	}
	.hk-wrapper.hk-menu-toggle {
		.card.fullscreen {
			left: 269px;
			right: -269px;
		}
	}
}
@media (max-width: 576px) {
	.card.card-lg {
		.card-header,
		.card-footer {
			padding: 0.75rem 1.25rem;
		}
		.card-body {
			padding: 1.25rem;
		}
	}
}
/*Placehoder*/
.placeholder {
	border-radius: 0.25rem;
}
/*Breadcrumb*/
.breadcrumb {
	.breadcrumb-item {
		font-size: 0.875rem;
		> a {
			color: $black;
			&:hover,
			&:focus {
				color: $primary;
			}
		}
		&.active {
			color: $grey_dark_3;
		}
	}
	&.breadcrumb-sm {
		.breadcrumb-item {
			font-size: 0.75rem;
		}
	}
	&.breadcrumb-dark {
		.breadcrumb-item {
			> a {
				@include color-opacity($white, 0.85);
				&:hover,
				&:focus {
					@include color-opacity($white, 1);
				}
			}
			&.active {
				@include color-opacity($white, 0.7);
			}
		}
		.breadcrumb-item + .breadcrumb-item {
			&:before {
				@include color-opacity($white, 0.3);
			}
		}
	}
	&.breadcrumb-full-width {
		border-radius: 0;
	}
}
.breadcrumb-item + .breadcrumb-item {
	padding-left: 0.3rem;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	display: flex;
	&:before {
		font-family: "dripicons-v2" !important;
		content: "V";
		padding-right: 0.3rem;
		position: relative;
		top: 1px;
		font-size: 0.625rem;
		@include color-opacity($grey_light_1, 0.8);
	}
}

/*Page Header*/
.hk-pg-header {
	padding: 1.25rem 0;
	background: $white;
	border-bottom: 1px solid $grey_light_4;
	.pg-title {
		font-size: 1.75rem;
		font-weight: 700;
		margin-bottom: 0.3rem;
	}
	.pg-title-sm {
		font-size: 1.5rem;
		font-weight: 700;
		margin-bottom: 0.25rem;
	}
	.pg-title-xs {
		font-size: 1.25rem;
		font-weight: 700;
		margin-bottom: 0.25rem;
	}
	.breadcrumb {
		padding-top: 0;
		padding-left: 0;
		padding-right: 0;
		margin-bottom: 0.5rem;
	}
	.pg-subtitle {
		font-size: 0.75rem;
		text-transform: uppercase;
		font-weight: 600;
		margin-bottom: 0.3rem;
	}
	.pg-stats-group {
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
		.stats-symbol {
			display: -ms-flexbox;
			display: flex;
			padding-right: 0.25rem;
			.feather-icon svg,
			.svg-icon svg {
				height: 13px;
				width: 13px;
			}
		}
		.stats-title {
			font-size: 0.875rem;
		}
		.stats-dot-sep {
			padding: 0 0.625rem;
			color: $grey_light_3;
		}
		.stats-line-sep {
			padding: 0 0.625rem;
			color: $grey_light_3;
		}
	}
	.pg-header-action-wrap {
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
	}
	&.pg-header-wth-tab {
		padding-bottom: 0;
		.nav-tabs {
			margin-top: 1.875rem;
			border-bottom: none;
		}
	}
	&.pg-header-wth-img {
		position: relative;
		// background-image:url(../img/cropper.jpg);
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		&:after {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background: $black_2;
			opacity: 0.6;
		}
		> div,
		> .nav-tabs {
			position: relative;
			z-index: 2;
		}
	}
	&.pg-header-wth-bg {
		background: $grey_light_5;
		border-bottom: none;
		.nav-tabs {
			.nav-item {
				.nav-link,
				.nav-link.active {
					border: none;
				}
				&.show {
					.nav-link {
						border: none;
					}
				}
			}
		}
	}
	&.pg-header-dark {
		.pg-title,
		.pg-title-sm,
		.pg-title-xs {
			color: $white;
		}
		.pg-subtitle,
		p {
			@include color-opacity($white, 0.8);
		}
		.pg-stats-group {
			.stats-symbol,
			.stats-title {
				@include color-opacity($white, 0.8);
			}
			.stats-line-sep,
			.stats-dot-sep {
				@include color-opacity($white, 0.3);
			}
		}
		.hk-vertical-separator-full:after {
			@include background-opacity($white, 0.2);
		}
	}
}

@media (max-width: 767px) {
	.hk-pg-header {
		.pg-title {
			font-size: 1.625rem;
		}
	}
}
