	padding-left: 0;
	padding-top: 1.25rem;
	background: $white;
}

#chart_year {
	float: left;
	position: relative;
	@include transition(1s ease transform);
	z-index: 3;
}

#chart_year.chart_quarter-activated {
	transform: translateX(0);
	@include transition(1s ease transform);
}

#chart_quarter {
	float: left;
	position: relative;
	z-index: -2;
	@include transition(1s ease transform);
}

#chart_quarter.active {
