			> i {
				font-size: 1.625rem;
				line-height: 20px;
			}
			span {
				display: inline-block;
				position: absolute;
				background-color: $blue;
				color: $white;
				padding: 0.25rem 0.5rem;
				font-size: 0.75rem;
				white-space: nowrap;
				top: 45px;
				left: 50%;
				min-width: 98px;
				display: none;
				border-radius: 0.25rem;
				@include transform(translateX(-50%));
			}
		}
		&:hover {
