			span {
				border: 1px solid $white;
			}
			&:focus {
				outline: none;
			}
			&:hover {
				span {
					background: $white;
				}
			}
			&.active {
				span {
					background: $white;
				}
			}
		}
	}
	&.theme-owl-dots {
		.owl-dots .owl-dot {
			span {
				border: 1px solid $primary;
			}
			&:focus {
				outline: none;
			}
			&:hover {
				span {
					background: $primary;
				}
			}
			&.active {
				span {
					background: $primary;
				}
			}
		}
	}
	.owl-video-play-icon {
		@include background-opacity($white, 0.8);
		left: 20px;
		bottom: 20px;
		margin: 0;
		height: 35px;
		width: 35px;
		border-radius: 50%;
		top: auto;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
		justify-content: center;
		-webkit-justify-content: center;
		-ms-flex-pack: center;
		color: $blue;
		&:before {
			display: inline-block;
			font: normal normal normal 22px/1 "Material-Design-Iconic-Font";
			text-rendering: auto;
			padding-left: 0.25rem;
			speak: none;
			text-transform: none;
			text-rendering: auto;
			line-height: 1;
			-webkit-font-smoothing: antialiased;
			-moz-osx-font-smoothing: grayscale;
			content: "\f3aa";
		}
		&:hover {
			@include transform(scale(1.1, 1.1));
		}
	}
	&.dots-on-item {
		.owl-dots {
			position: absolute;
			left: 0;
			right: 0;
