}
.react-datepicker__day {
	font-size: 0.875rem;
	color: $grey_dark_3;
}
.datepicker__quarter-text {
	color: $grey_dark_1;
}
.react-datepicker__current-month,
.react-datepicker-time__header,
.react-datepicker-year-header {
	font-size: 0.875rem;
	letter-spacing: 0.5px;
	font-weight: 600;
}
.react-datepicker__day-name {
	color: $grey_dark_3;
	font-size: 15px;
