/*Activity*/
.activity-list {
	.list-group-item {
		padding: 0;
		border: none;
		.media {
			position: relative;
			align-items: inherit;
			.media-head {
				position: relative;
				margin-right: 1rem;
				display: -webkit-box;
				display: -ms-flexbox;
				display: flex;
				flex-flow: column;
				min-width: 48px;
				align-items: center;
				> .avatar {
					flex-shrink: 0;
				}
			}
			.media-body {
				padding-bottom: 1.5rem;
				.last-activity-time {
					font-size: 0.75rem;
					margin-top: 0.25rem;
				}
			}
		}
		&.toggle-activity {
			.media .media-body {
				padding-bottom: 0;
				display: flex;
				align-items: center;
			}
		}
		&:last-child {
			.media {
				.media-head {
					&:after {
						display: none;
					}
				}
			}
		}
	}
	&.activity-wth-line {
		.list-group-item {
			.media {
				.media-head {
					&:after {
						content: "";
						background: $grey_light_3;
						width: 2.5px;
						height: 100%;
						border-radius: 50rem;
						margin: 0.5rem auto;
						min-height: 25px;
					}
				}
			}
		}
	}
}
.activity-list-h {
	flex-direction: row;
	.list-group-item {
		padding: 0;
		border: none;
		display: flex;
		flex-direction: column;
		padding-bottom: 6.25rem;
		align-items: center;
		.media {
			flex-flow: column;
			align-items: center;
			flex: 1;
			.media-body {
				width: 400px;
				padding: 0 0.625rem;
				display: flex;
				.last-activity-time {
					font-size: 0.75rem;
					margin-top: 0.25rem;
				}
				.card {
					width: 100%;
				}
			}
			.media-head {
				width: 100%;
				display: flex;
				justify-content: center;
				position: relative;
				min-height: 48px;
				align-items: center;
				position: absolute;
				bottom: 45px;
				&:after,
				&:before {
					content: "";
					height: 2.5px;
					background: $grey_light_3;
					width: 100%;
					position: absolute;
					margin: 0.5rem auto;
					right: 0;
					width: 50%;
					top: 0;
					bottom: 0;
					margin: auto 0;
				}
				&:before {
					left: 0;
				}
				.avatar {
					z-index: 1;
					outline: 7px solid $white;
				}
			}
		}
		&:first-child {
			.media {
				.media-head {
					&:before {
						display: none;
					}
				}
			}
		}
		&:last-child {
			.media {
				.media-head {
					&:after {
						display: none;
					}
				}
			}
		}
		.activity-time {
			text-align: center;
			font-size: 0.875rem;
			font-weight: 500;
			padding: 0.5rem 0;
			color: $primary;
			text-align: center;
			position: absolute;
			bottom: 0;
		}
	}
}
.user-activity {
	.media {
		position: relative;
		padding-bottom: 1.25rem;
		margin-left: 1.25rem;
		.media-head {
			position: absolute;
			left: -20px;
		}
		&:after {
			content: "";
			position: absolute;
			width: 1px;
			height: calc(100% - 77px);
			background: $grey_light_3;
			top: 60px;
		}
		.media-body {
			padding-bottom: 1.875rem;
			margin-left: 2.5rem;
			min-width: 0;
		}
		&:last-child {
			.media-body {
				border-bottom: none;
			}
		}
	}
	&.user-activity-sm {
		.media {
			padding-bottom: 0.625rem;
			margin-left: 1rem;
			.media-head {
				left: -16px;
			}
			.media-body {
				padding-bottom: 0.625rem;
				margin-left: 1.875rem;
			}
		}
	}
}
@media (max-width: 576px) {
	.user-activity .media .media-body {
		margin-left: 2.125rem;
	}
}
/*Wizards*/
.hk-wizard {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-size: 0.875rem;
	font-variant: tabular-nums;
	line-height: 1.5715;
	list-style: none;
	font-feature-settings: "tnum";
	display: flex;
	width: 100%;
	font-size: 0;
	text-align: initial;
	.hk-wizard-item {
		&:not(.hk-wizard-item-active) {
			> .hk-wizard-item-container[role="button"] {
				cursor: pointer;
				.hk-wizard-item-title,
				.hk-wizard-item-subtitle,
				.hk-wizard-item-description,
				.hk-wizard-item-icon {
					.hk-wizard-icon {
						@include transition(color 0.3s);
					}
				}
				&:hover {
					.hk-wizard-item-title,
					.hk-wizard-item-subtitle,
					.hk-wizard-item-description {
						color: $primary;
					}
				}
			}
			&:not(.hk-wizard-item-process) {
				> .hk-wizard-item-container[role="button"] {
					&:hover {
						.hk-wizard-item-icon {
							border-color: $primary;
							.hk-wizard-icon {
								color: $primary;
							}
						}
					}
				}
			}
		}
	}
	&:not(.hk-wizard-vertical) {
		.hk-wizard-item-custom {
			.hk-wizard-item-icon {
				width: auto;
				background: 0 0;
			}
		}
	}
}
.hk-wizard-item {
	position: relative;
	display: inline-block;
	flex: 1;
	overflow: hidden;
	vertical-align: top;
	&:last-child {
		flex: none;
		> .hk-wizard-item-container {
			> .hk-wizard-item-tail {
				display: none;
			}
			> .hk-wizard-item-content {
				> .hk-wizard-item-title {
					&:after {
						display: none;
					}
				}
			}
		}
	}
}
.hk-wizard-item-container {
	outline: none;
}
.hk-wizard-item-icon {
	display: inline-block;
	vertical-align: top;
	width: 32px;
	height: 32px;
	margin: 0 0.5rem 0 0;
	font-size: 1rem;
	line-height: 32px;
	text-align: center;
	border: 1px solid $grey_light_2;
	border-radius: 2rem;
	transition: background-color 0.3s, border-color 0.3s;
	.hk-wizard-icon {
		position: relative;
		top: -0.5px;
		color: $primary;
	}
}
.hk-wizard-item-content {
	display: inline-block;
	vertical-align: top;
}
.hk-wizard-item-tail {
	position: absolute;
	top: 12px;
	left: 0;
	width: 100%;
	padding: 0 0.625rem;
	&:after {
		display: inline-block;
		width: 100%;
		height: 1px;
		background: $grey_light_3;
		border-radius: 1px;
		@include transition(background 0.3s);
		content: "";
	}
}
.hk-wizard-item-title {
	position: relative;
	display: inline-block;
	padding-right: 1rem;
	font-size: 1rem;
	line-height: 32px;
	color: $black_2;
	&:after {
		position: absolute;
		top: 16px;
		left: 100%;
		display: block;
		width: 9999px;
		height: 1px;
		background: $grey_light_3;
		content: "";
	}
}
.hk-wizard-item-subtitle {
	display: inline;
	margin-left: 0.5rem;
	font-weight: 400;
	font-size: 0.875rem;
}
.hk-wizard-item-description {
	font-size: 0.875rem;
}
.hk-wizard-item-wait {
	> .hk-wizard-item-container > .hk-wizard-item-content > .hk-wizard-item-title {
		color: $grey_dark_3;
	}
	.hk-wizard-item-icon {
		background-color: $white;
		border-color: $grey_light_2;
		> .hk-wizard-icon {
			color: $grey_light_2;
			.hk-wizard-icon-dot {
				background: $grey_light_2;
			}
		}
	}
	> .hk-wizard-item-container {
		> .hk-wizard-item-content {
			> .hk-wizard-item-title {
				&:after {
					background-color: $grey_light_3;
				}
			}
		}
		> .hk-wizard-item-tail {
			&:after {
				background-color: $grey_light_3;
			}
		}
	}
}
.hk-wizard-item-process {
	.hk-wizard-item-icon {
		background-color: $white;
		border-color: $primary;
		> .hk-wizard-icon {
			color: $primary;
			.hk-wizard-icon-dot {
				background: $primary;
			}
		}
	}
	> .hk-wizard-item-container {
		> .hk-wizard-item-content {
			> .hk-wizard-item-title {
				color: $black_2;
				&:after {
					background-color: $grey_light_3;
				}
			}
			> .hk-wizard-item-description {
				color: $black_2;
			}
		}
		> .hk-wizard-item-tail {
			&:after {
				background-color: $grey_light_3;
			}
		}
		> .hk-wizard-item-icon {
			background: $primary;
			.hk-wizard-icon {
				color: $white;
			}
		}
		> .hk-wizard-item-title {
			font-weight: 500;
		}
	}
}
.hk-wizard-item-finish {
	.hk-wizard-item-icon {
		background-color: $white;
		border-color: $primary;
		> .hk-wizard-icon {
			color: $primary;
			.hk-wizard-icon-dot {
				background: $primary;
			}
		}
	}
	> .hk-wizard-item-container {
		> .hk-wizard-item-content {
			> .hk-wizard-item-title {
				color: $black_2;
				&:after {
					background-color: $primary;
				}
			}
		}
		> .hk-wizard-item-tail {
			&:after {
				background-color: $primary;
			}
		}
	}
}
.hk-wizard-item-error {
	.hk-wizard-item-icon {
		background-color: $white;
		border-color: $red;
		> .hk-wizard-icon {
			color: $red;
			.hk-wizard-icon-dot {
				background: $red;
			}
		}
	}
	> .hk-wizard-item-container {
		> .hk-wizard-item-content {
			> .hk-wizard-item-title {
				color: $red;
				&:after {
					background-color: $grey_light_3;
				}
			}
			> .hk-wizard-item-description {
				color: $red;
			}
		}
		> .hk-wizard-item-tail {
			&:after {
				background-color: $grey_light_3;
			}
		}
	}
}
.hk-wizard-item.hk-wizard-next-error {
	.hk-wizard-item-title {
		&:after {
			background: $red;
		}
	}
}
.hk-wizard-item-disabled {
	cursor: not-allowed;
}
.hk-wizard-horizontal {
	&:not(.hk-wizard-label-vertical) {
		.hk-wizard-item {
			padding-left: 1rem;
			white-space: nowrap;
			&:first-child {
				padding-left: 0;
			}
			&:last-child {
				.hk-wizard-item-title {
					padding-right: 0;
				}
			}
		}
		.hk-wizard-item-tail {
			display: none;
		}
		.hk-wizard-item-description {
			max-width: 140px;
			white-space: normal;
		}
	}
}
.hk-wizard-item-custom {
	> .hk-wizard-item-container {
		> .hk-wizard-item-icon {
			height: auto;
			background: 0 0;
			border: 0;
			> .hk-wizard-icon {
				top: 0;
				left: 0.5px;
				width: 32px;
				height: 32px;
				font-size: 1.5rem;
				line-height: 32px;
			}
		}
	}
}
.hk-wizard-item-custom.hk-wizard-item-process {
	.hk-wizard-item-icon {
		> .hk-wizard-icon {
			color: $primary;
		}
	}
}
.hk-wizard-sm.hk-wizard-horizontal {
	&:not(.hk-wizard-label-vertical) {
		.hk-wizard-item {
			padding-left: 0.75rem;
			&:first-child {
				padding-left: 0;
			}
		}
	}
}
.hk-wizard-sm {
	.hk-wizard-item-icon {
		width: 24px;
		height: 24px;
		margin: 0 0.5rem 0 0;
		font-size: 0.75rem;
		line-height: 24px;
		text-align: center;
		border-radius: 1.5rem;
	}
	.hk-wizard-item-title {
		padding-right: 0.75rem;
		font-size: 0.875rem;
		line-height: 24px;
		&:after {
			top: 12px;
		}
	}
	.hk-wizard-item-description {
		font-size: 0.875rem;
	}
	.hk-wizard-item-tail {
		top: 8px;
	}
	.hk-wizard-item-custom {
		.hk-wizard-item-icon {
			width: inherit;
			height: inherit;
			line-height: inherit;
			background: 0 0;
			border: 0;
			border-radius: 0;
			> .hk-wizard-icon {
				font-size: 1.5rem;
				line-height: 24px;
				transform: none;
			}
		}
	}
}
.hk-wizard-vertical {
	display: flex;
	flex-direction: column;
	> .hk-wizard-item {
		display: block;
		flex: 1 0 auto;
		padding-left: 0;
		overflow: visible;
		.hk-wizard-item-icon {
			float: left;
			margin-right: 1rem;
		}
		.hk-wizard-item-content {
			display: block;
			min-height: 48px;
			overflow: hidden;
		}
		.hk-wizard-item-title {
			line-height: 32px;
		}
		.hk-wizard-item-description {
			padding-bottom: 0.75rem;
		}
		> .hk-wizard-item-container {
			> .hk-wizard-item-tail {
				position: absolute;
				top: 0;
				left: 16px;
				width: 1px;
				height: 100%;
				padding: 2.375rem 0 0.375rem;
				&:after {
					width: 1px;
					height: 100%;
				}
			}
			> .hk-wizard-item-content {
				> .hk-wizard-item-title {
					&:after {
						display: none;
					}
				}
			}
		}
		&:not(:last-child) {
			> .hk-wizard-item-container {
				> .hk-wizard-item-tail {
					display: block;
				}
			}
		}
	}
}
.hk-wizard-vertical.hk-wizard-sm {
	.hk-wizard-item-container {
		.hk-wizard-item-tail {
			position: absolute;
			top: 0;
			left: 12px;
			padding: 1.875rem 0 0.375rem;
		}
		.hk-wizard-item-title {
			line-height: 24px;
		}
	}
}
.hk-wizard-label-vertical {
	.hk-wizard-item {
		overflow: visible;
	}
	.hk-wizard-item-tail {
		margin-left: 3.625rem;
		padding: 0.25rem 1.5rem;
	}
	.hk-wizard-item-content {
		display: block;
		width: 116px;
		margin-top: 0.5rem;
		text-align: center;
	}
	.hk-wizard-item-icon {
		display: inline-block;
		margin-left: 2.625rem;
	}
	.hk-wizard-item-title {
		padding-right: 0;
		padding-left: 0;
		&:after {
			display: none;
		}
	}
	.hk-wizard-item-subtitle {
		display: block;
		margin-bottom: 4px;
		margin-left: 0;
		line-height: 1.5715;
	}
}
.hk-wizard-label-vertical.hk-wizard-sm {
	&:not(.hk-wizard-dot) {
		.hk-wizard-item-icon {
			margin-left: 2.875rem;
		}
	}
}
.hk-wizard-dot {
	.hk-wizard-item-title {
		line-height: 1.5715;
	}
	.hk-wizard-item-tail {
		top: 2px;
		width: 100%;
		margin: 0 0 0 4.375rem;
		padding: 0;
		&:after {
			width: calc(100% - 20px);
			height: 3px;
			margin-left: 0.75rem;
		}
	}
	.hk-wizard-item {
		&:first-child {
			.hk-wizard-icon-dot {
				left: 2px;
			}
		}
	}
	.hk-wizard-item-icon {
		width: 8px;
		height: 8px;
		margin-left: 4.25rem;
		padding-right: 0;
		line-height: 8px;
