/*Dropdown*/
.dropdown-toggle {
	&.no-caret {
		&:after {
			display: none;
		}
	}
	&:after {
		content: "\f2f9";
		font: normal normal normal 1rem/1 "Material-Design-Iconic-Font";
		border: none !important;
		vertical-align: 0 !important;
		margin-left: 0.4em;
	}
	&.dropdown-toggle-split {
		&:after,
		&:before {
			margin: 0 !important;
		}
	}
}
.dropup .dropdown-toggle::after {
	content: "\f2fc";
	margin-left: 0.4em;
}
.dropstart {
	.dropdown-toggle::before {
		content: "\f2fa";
		vertical-align: 0 !important;
		font: normal normal normal 1rem/1 "Material-Design-Iconic-Font";
		border: none !important;
		margin-right: 0.4em;
	}
	.dropdown-toggle::after {
		display: none;
	}
}
.dropend .dropdown-toggle::after {
	content: "\f2fb";
	vertical-align: 0;
	margin-left: 0.4em;
}
.btn {
	&.btn-lg,
	&.btn-sm,
	&.btn-xs {
		&.dropdown-toggle {
			&:after,
			&:before {
				font-size: inherit;
			}
		}
	}
}
.dropdown-menu {
	color: $grey_dark_3;
	border: none;
	border-radius: 0.5rem;
	padding: 0.5rem;
	box-shadow: 0px 6px 16px rgba(47, 52, 58, 0.1);
	.dropdown-item-text {
		color: $grey_dark_3;
		padding: 0.5rem 0.75rem;
	}
	.dropdown-header {
		color: $grey;
		padding: 0.5rem 0.75rem;
		font-weight: 500;
		&.dropdown-header-bold {
			font-size: 0.75rem;
			text-transform: uppercase;
			font-weight: 600;
		}
		&.header-wth-bg {
			background: $black_2;
			color: $white;
			margin-top: -0.5rem;
			margin-left: -0.5rem;
			margin-right: -0.5rem;
			margin-bottom: 0.5rem;
			border-top-left-radius: 0.5rem;
			border-top-right-radius: 0.5rem;
			padding-top: 0.8rem;
			padding-bottom: 0.8rem;
		}
	}
	&.dropdown-bordered {
		border: 1px solid $grey_light_3;
		box-shadow: none;
	}
	&.dropdown-menu-icon-text {
		.dropdown-item {
			.dropdown-item-text {
				padding: 0;
			}
		}
	}
	&.dropdown-menu-avatar {
		.dropdown-item {
			padding-top: 0.3rem;
			padding-bottom: 0.3rem;
			.dropdown-item-text {
				padding: 0;
			}
			.avatar {
				margin-right: 1rem;
			}
		}
	}
	.dropdown-item {
		color: $black_2;
		cursor: pointer;
		border-radius: 0.25rem;
		padding: 0.25rem 0.75rem;
		.dropdown-icon {
			font-size: 1.125rem;
			margin-right: 0.625rem;
			width: 21px;
			position: relative;
			top: 2px;
			&.feather-icon,
			.svg-icon {
				top: -2px;
			}
		}
		&.active,
		&:active {
			background-color: $primary;
			color: $white;
		}
		&:focus:not(.active):not(.disabled),
		&:hover:not(.active):not(.disabled) {
			background-color: $grey_light_5;
			color: $black_2;
		}
		&:disabled,
		&.disabled {
			color: $grey_light_1;
			background-color: transparent;
		}
	}
	.sub-dropdown-menu {
		position: relative;
		.dropdown-menu {
			&:before,
			&:after {
				display: none !important;
			}
			top: 0;
			&.open-left-side {
				right: 100% !important;
				left: auto !important;
			}
			&.open-right-side {
				left: 100% !important;
			}
		}
	}
	&.dropdown-menu-dark {
		background: $black_2;
		border-color: $black_2;
		color: $white;
		.dropdown-header,
		.dropdown-item-text {
			color: $grey_dark_3;
		}
		.dropdown-item {
			color: $grey_light_4;
			&:focus:not(.active):not(.disabled),
			&:hover:not(.active):not(.disabled) {
				background-color: $grey_dark_5;
				color: $white;
			}
			&.active,
			&:active {
				color: $white;
			}
			&:disabled,
			&.disabled {
				color: $grey_dark_4;
			}
		}
		.dropdown-divider {
			border-color: $black_2;
		}
	}
}
.dropdown-divider {
	border-color: $grey_light_4;
	margin-right: -0.5rem;
	margin-left: -0.5rem;
}
.show-on-hover {
	&:hover > .dropdown-menu {
		display: block;
	}
	> .dropdown-toggle:active {
		pointer-events: none;
	}
}
.dropdown-menu.animated {
	-webkit-animation-duration: 0.55s;
	animation-duration: 0.55s;

	-webkit-animation-timing-function: ease;
	animation-timing-function: ease;
}
.custom-caret {
	svg {
		height: 19px;
		width: 19px;
	}
}
.btn-sm {
	.custom-caret {
		svg {
			height: 17px;
			width: 17px;
		}
	}
}
.btn-group-lg > .btn + .dropdown-toggle-split,
.btn-lg + .dropdown-toggle-split {
	padding-right: 1.3rem;
	padding-left: 1.3rem;
}
.btn-group-sm > .btn + .dropdown-toggle-split,
.btn-sm + .dropdown-toggle-split {
	padding-right: 0.8rem;
	padding-left: 0.8rem;
}
