/*Chips*/
.chip {
	display: inline-block;
	position: relative;
	vertical-align: middle;
	> span {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
		justify-content: center;
		-webkit-justify-content: center;
		-ms-flex-pack: center;
		position: relative;
		padding: 0.25rem 0.75rem;
		font-size: 0.875rem;
		line-height: 1.5;
		border-radius: 0.5rem;
		color: $black_2;
	}
	.chip-text {
		pointer-events: none;
	}
	input {
		position: absolute;
		height: 100%;
		width: 100%;
		left: 0;
		top: 0;
		opacity: 0;
		cursor: pointer;
		+ span {
			pointer-events: none;
		}
	}
	&.chip-primary {
		> span {
			background-color: $primary_light_5;
			border: 1px solid $primary_light_5;
			color: $primary;
		}
		&:hover {
			input {
				+ span {
					border-color: $primary_light_4;
					background-color: $primary_light_4;
				}
			}
		}
		input {
			&:checked + span {
				color: $white;
				border-color: $primary;
				background-color: $primary;
			}
		}
	}
	&.chip-secondary {
		> span {
			background-color: $grey_light_5;
			border: 1px solid $grey_light_5;
			color: $black_2;
		}
		&:hover {
			input {
				+ span {
					border-color: $grey_light_4;
					background-color: $grey_light_4;
				}
			}
		}
		input {
			&:checked + span {
				color: $white;
				border-color: $black_2;
				background-color: $black_2;
			}
		}
	}
	&.chip-outline-primary {
		> span {
			background: transparent;
			border: 1px solid $primary_light_3;
			color: $primary;
		}
		&:hover {
			input {
				+ span {
					border-color: $primary;
				}
			}
		}
		input {
			&:checked + span {
				color: $white;
				border-color: $primary;
				background-color: $primary;
			}
		}
	}
	&.chip-outline-secondary {
		> span {
			background: transparent;
			border: 1px solid $grey_light_1;
			color: $black_2;
		}
		&:hover {
			input {
				+ span {
					border-color: $black_2;
				}
			}
		}
		input {
			&:checked + span {
				color: $white;
				border-color: $black_2;
				background-color: $black_2;
			}
		}
	}
	&.chip-wth-icon {
		> span {
			i,
			.feather-icon,
			.svg-icon {
				font-size: 1rem;
				line-height: 1;
				margin-right: 0.5rem;
				display: flex;
			}
			.feather-icon > svg,
			.svg-icon > svg {
				height: 1em;
				width: 1em;
			}
		}
	}
	&.chip-ticked {
		> span {
			&:before {
				font: normal normal normal 0/1 "Material-Design-Iconic-Font";
				-webkit-font-smoothing: antialiased;
				-moz-osx-font-smoothing: grayscale;
				content: "\f26b";
				@include transition(all 0.15s ease);
			}
			.chip-text {
				@include transition(all 0.15s ease);
			}
		}
		input {
			&:checked + span {
				&:before {
					font-size: 1rem;
				}
				.chip-text {
					margin-left: 0.5rem;
				}
			}
		}
	}
	&.chip-dismissable {
		.btn-close {
			margin-left: 0.5rem;
			color: inherit !important;
		}
	}
	&.chip-pill {
		> span {
			border-radius: 50rem;
		}
	}
	&.chip-disabled {
		cursor: not-allowed;
		input {
			pointer-events: none;
		}
		> span {
			border-color: $grey_light_5 !important;
			background-color: $grey_light_5 !important;
			color: $grey_light_1 !important;
			.btn-close {
				color: $grey_light_1 !important;
				pointer-events: none;
			}
		}
	}
	&.user-chip {
		> span {
			border-radius: 50rem;
			.avatar {
				height: 100%;
				width: auto;
				padding: 0.2rem;
				position: absolute;
				left: 0;
				img {
					border-radius: 50%;
				}
			}
			.chip-text {
				margin-left: 1.375rem;
			}
		}
	}
	&.chip-lg {
		> span {
			padding: 0.5rem 1rem;
			font-size: 1rem;
		}
		&.chip-wth-icon {
			> span {
				i,
				svg {
					font-size: 1.125rem;
				}
			}
		}
		&.chip-ticked {
			input {
				&:checked + span {
					&:before {
						font-size: 1.25rem;
					}
				}
			}
		}
		&.user-chip {
			> span {
				.chip-text {
					margin-left: 1.875rem;
				}
			}
		}
	}
}
