/*Ribbons*/
.hk-ribbon-type-1 {
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	left: 20px;
	background: $primary;
	color: $white;
	padding: 0.25rem 0.75rem;
	border-radius: 0.5rem;
	font-size: 0.75rem;
	min-height: 26px;
	top: 0;
	text-align: center;
	@include transform(translateY(-50%));
	> span {
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
		position: relative;
		z-index: 1;
	}
	&.ribbon-wth-flag {
		&:before {
			background-color: transparent;
			top: 0;
			bottom: 0;
			content: "";
			position: absolute;
			border-top: 13px solid $primary;
			border-bottom: 13px solid $primary;
			border-right: 13px solid $primary;
			border-left: 0.6rem solid transparent !important;
			left: -14px;
		}
		&:after {
			background-color: transparent;
			border: 13px solid $primary;
			top: 0;
			bottom: 0;
			content: "";
			position: absolute;
			right: -14px;
			border-top: 13px solid $primary;
			border-bottom: 13px solid $primary;
			border-left: 13px solid $primary;
			border-right: 0.6rem solid transparent !important;
		}
