/*Advance Badges*/
.badge-icon {
	width: 4rem;
	height: 4.313rem;
	font-size: 2.25rem;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	> svg {
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		height: 100%;
		width: 100%;
	}
	.badge-icon-wrap {
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: center;
		justify-content: center;
		-webkit-justify-content: center;
		-ms-flex-pack: center;
		height: 100%;
		width: 100%;
		color: $white;
		position: relative;
		z-index: 1;
	}
	&.badge-icon-lg {
		width: 6rem;
		height: 6.031rem;
		font-size: 3.25rem;
	}
	&.badge-icon-md {
		width: 5rem;
		height: 5.031rem;
		font-size: 2.75rem;
	}
	&.badge-icon-sm {
		width: 3rem;
		height: 3.313rem;
		font-size: 1.75rem;
	}
	&.badge-icon-xs {
		width: 2rem;
		height: 2.313rem;
		font-size: 1.25rem;
	}
	&.badge-icon-xxs {
		width: 1.5rem;
		height: 1.813rem;
		font-size: 1rem;
	}
	&.badge-circle {
		height: 4rem;
		&.badge-icon-lg {
			height: 6rem;
		}
		&.badge-icon-md {
			height: 5rem;
		}
		&.badge-icon-sm {
			height: 3rem;
		}
		&.badge-icon-xs {
			height: 2rem;
		}
		&.badge-icon-xxs {
			height: 1.5rem;
		}
	}
	&.badge-level {
		position: relative;
		> span {
			position: absolute;
			bottom: 0;
			right: 16%;
			z-index: 1;
			font-weight: 600;
			color: inherit;
			font-size: 40%;
			background: transparent;
			&:before {
				content: attr(title);
				position: absolute;
				-webkit-text-stroke: 4px $white;
				left: 0;
				z-index: -1;
			}
		}
	}
}
.avatar {
	&.custom-badge-on-avatar {
		.badge-icon {
			font-size: inherit;
		}
	}
}

/*Callout*/
.callout {
	margin-bottom: 0;
	&.callout-floating {
		position: fixed;
		width: 300px;
		z-index: 1055;
		margin-bottom: 0;
		&.top-end {
			top: 25px;
			right: 25px;
		}
		&.bottom-end {
			bottom: 25px;
			right: 25px;
		}
		&.top-start {
			top: 25px;
			left: 25px;
		}
		&.bottom-start {
			bottom: 25px;
			left: 25px;
		}
		&.top-center {
			top: 25px;
			left: 50%;
			width: auto;
			@include transform(translateX(-50%));
		}
		&.bottom-center {
			bottom: 25px;
			left: 50%;
			width: auto;
			@include transform(translateX(-50%));
		}
	}
	&.callout-dark {
		color: $white;
		h1,
		h2,
		h3,
		h4,
		h5,
		h6,
		p {
			color: inherit;
		}
	}
}
