	}
}

/*Filter Component*/
.btn-filter {
	color: $grey_dark_3;
	background-color: transparent;
	background-image: none;
	border-color: $grey_light_5;
	&:hover,
	&:focus {
		color: $grey_dark_3;
		background-color: transparent;
		background-image: none;
		border-color: $grey_light_5;
	}
	&.active {
		color: $primary;
		background-color: $primary_light_5;
		border-color: $primary;
		box-shadow: none !important;
		&:hover,
		&:focus {
			color: $primary;
			background-color: $primary_light_5;
			border-color: $primary;
		}
	}
}
.filter-block-wrap {
	.filter-block {
		&.card {
			.card-img {
				-o-object-fit: cover;
				object-fit: cover;
			}
			.card-img-overlay {
				background-color: rgba($black_2, var(--bs-bg-opacity));
			}
			input {
				position: absolute;
				z-index: 1;
				left: 0;
				top: 0;
				margin: 0;
				height: 100%;
				width: 100%;
				opacity: 0;
				cursor: pointer;
				&:checked ~ .card-img-overlay {
					background-color: rgba($blue, var(--bs-bg-opacity));
				}
			}
		}
	}
}
