		.hk-content-sidebar {
			position: fixed;
			top: 64px;
			overflow-y: auto;
			width: 280px;
			right: 0;
			z-index: 2;
			background: $white;
			padding-top: 1.5rem;
			border-left: 1px solid $grey_light_3;
			bottom: 0;
			.sidebar-content-wrap {
				padding: 1.25rem;
			}
		}
	}
}

/*Fixed Footer*/
.hk-fixed-footer {
	position: fixed;
	bottom: 0;
	z-index: 1032;
	background: $white;
	box-shadow: 1px 1px 3px 5px rgba(0, 0, 0, 0.1);
	right: 0;
	left: 0;
