/*Dragula*/
.gu-mirror {
	cursor: -webkit-grabbing;
	cursor: -moz-grabbing;
	cursor: grabbing;
	position: fixed !important;
	margin: 0 !important;
	z-index: 9999 !important;
	opacity: 1;
}
.dragula-demo {
	.dragula-block {
		border: 1px dotted $grey_light_1;
		border-radius: 0.25rem;
		padding: 0.625rem;
	}
	.dragula-block > div {
		border: 1px solid $grey_light_3;
		border-radius: 0.25rem;
		margin: 0.625rem;
		padding: 0.625rem;
		cursor: move;
	}
}

/*Authantication Pages*/
.hk-wrapper {
	&.hk-pg-auth {
		.hk-pg-wrapper {
			.hk-pg-body {
				.auth-content {
					min-height: calc(100vh - 249px);
					display: -webkit-box;
					display: -ms-flexbox;
					display: flex;
					-webkit-box-align: center;
					-ms-flex-align: center;
					align-items: center;
					justify-content: center;
					-webkit-justify-content: center;
					-ms-flex-pack: center;
				}
			}
			.v-separator {
				margin: 0;
				position: relative;
				&:after {
					height: 100%;
					right: 0;
				}
				&.separator-sm {
					&:after {
						height: 70%;
					}
				}
			}
			.auth-split {
				min-height: 100vh;
				.auth-content {
					min-height: 100vh;
					display: -webkit-box;
					display: -ms-flexbox;
					display: flex;
					-webkit-box-align: center;
					-ms-flex-align: center;
					align-items: center;
					justify-content: center;
					-webkit-justify-content: center;
					-ms-flex-pack: center;
					position: relative;
					mark {
						z-index: 1;
						color: $black_2;
						&:after {
							z-index: -1;
							background: $green;
							opacity: 1;
						}
					}
					.extr-link {
						font-size: 0.75rem;
						display: -ms-flexbox;
						display: flex;
						-webkit-box-align: center;
						-ms-flex-align: center;
						align-items: center;
						justify-content: flex-end;
						-webkit-justify-content: flex-end;
						.feather-icon,
						.svg-icon {
							margin-right: 0.25rem;
							svg {
								height: 14px;
								width: 14px;
							}
						}
					}
				}
				.credit-text {
					position: absolute;
					left: 30px;
					bottom: 30px;
					mix-blend-mode: inherit;
				}
				.bg-img {
					position: absolute;
					left: 0;
					top: 0;
					max-width: 100%;
					object-fit: cover;
					height: 100%;
					z-index: -1;
				}
			}
		}
	}
}
@media (max-width: 767px) {
	.hk-wrapper {
		&.hk-pg-auth {
			.hk-pg-wrapper {
				.hk-pg-body {
					.container-xxl {
						.auth-content {
							min-height: auto;
							-webkit-box-align: flex-start;
							-ms-flex-align: flex-start;
							align-items: flex-start;
						}
					}
				}
			}
			.v-separator:after {
				display: none;
			}
		}
	}
}
